<template>
    <!--  -->
    <div class="home">
        <div class="content" :style="`background:${background}`" v-loading="loading">
            <div style="text-align: left; width: 800px">
                <p>:</p>
                <p style="font-size: 12px">
                    ,,,,
                </p>
            </div>
            <canvas id="canvas" ref="canvas" width="800" height="800"></canvas>
        </div>
        <div class="operations">
            <div style="height: 800px">
                <EasyForm :formParams="formParams">
                    <template #inputFile="{}">
                        <el-button type="primary" size="small" @click="inputFile">
                            
                        </el-button>
                        <div>{{ imgList.length }}</div>
                    </template>
                </EasyForm>
                <el-button type="primary" size="default" @click="reload"></el-button>
                <el-button type="primary" size="default" @click="generateImg"></el-button>
                <el-button type="success" size="default" @click="exportCanvas"></el-button>
            </div>
        </div>
    </div>
</template>

<script>
/* eslint-disable */
// @ is an alias to /src
import { getMostColor } from "@utils/mostColor.js";
import { getAverageColor } from "@utils/averageColor.js";
import { inputFile } from "@utils/inputFile.js";
import { mostBlockColor } from "@utils/mostBlockColor.js";
import { fabric } from "fabric";
export default {
    name: "Home",
    components: {},
    data() {
        return {
            imgList: [], //
            blockList: [], //
            blockMainColors: [], //
            loading: false,
            formParams: {
                data: {}, // 
                formList: {
                    targetFile: {
                        type: "upload",
                        label: "",
                        limit: 1,
                        action: "#",
                        listType: "file-list",
                        fileList: [],
                        autoUpload: false,
                        onChange: this.slectFile,
                    },
                    inputFile: {
                        type: "customItem",
                        label: "",
                        name: "inputFile",
                    },
                    /* userN2ww2ame: {
                        type: "slider",
                        label: "",
                    }, */
                    //TODO
                    aaa: {
                        type: "select",
                        label: "",
                        placeholder: "",
                        selectOptions: [],
                        disabled: true,
                    },
                    bbb: {
                        type: "select",
                        label: "",
                        placeholder: "",
                        selectOptions: [],
                        disabled: true,
                    },
                    ccc: {
                        type: "switch",
                        label: ":",
                        placeholder: "",
                        disabled: true,
                    },
                    ddd: {
                        type: "switch",
                        label: "",
                        disabled: true,
                    },
                    eee: {
                        type: "select",
                        label: "",
                        selectOptions: [],
                        disabled: true,
                    },
                    fff: {
                        type: "slider",
                        label: "",
                        disabled: true,
                    },
                    ggg: {
                        type: "slider",
                        label: "",
                        disabled: true,
                    },
                },

                labelWidth: "90px",
                rules: {},
            },
            background: "#91A8D0",
            ctx: null,
            canvas: null,
        };
    },
    mounted() {
        this.initCanvas();
    },
    methods: {
        //
        initCanvas() {
            this.canvas = new fabric.Canvas("canvas", {
                isDrawingMode: false, //
                selectable: false,
                selection: false,
                hoverCursor: "pointer",
                devicePixelRatio: true, //Retina  
                stroke: "lightgreen",
                strokeWidth: 4,
            });
            this.ctx = canvas.getContext("2d");
            this.canvas.freeDrawingBrush.color = "blue";
            this.canvas.freeDrawingBrush.width = 5;
            this.addCanvasEvent(); //
        },
        //
        reload() {
            // window.location.reload();
            console.log(this.canvas.viewportTransform);
            this.canvas.deactivateAll().renderAll();
            //http://fabricjs.com/fabric-intro-part-5#pan_zoom
            // this.canvas.clear(); // 
        },
        //
        generateImg() {
            this.loading = true;
            let diffColorList = [];
            //
            for (let i = 0; i < this.blockMainColors.length; i++) {
                diffColorList[i] = { diffs: [] };
                //
                for (let j = 0; j < this.imgList.length; j++) {
                    diffColorList[i].diffs.push({
                        url: this.imgList[j].url,
                        diff: this.colorDiff(this.blockMainColors[i].color, this.imgList[j].color),
                        color: this.imgList[j].color,
                    });
                }
                //,
                diffColorList[i].diffs.sort((a, b) => {
                    return a.diff - b.diff;
                });
                //0
                diffColorList[i].url = diffColorList[i].diffs[0].url;
                diffColorList[i].position = this.blockMainColors[i].position;
                diffColorList[i].Acolor = this.blockMainColors[i].color;
                diffColorList[i].Bcolor = diffColorList[i].diffs[0].color;
            }
            this.loading = false;
            console.log(diffColorList);
            //,
            diffColorList.forEach((item) => {
                fabric.Image.fromURL(item.url, (img) => {
                    let scale = img.height > img.width ? 8 / img.width : 8 / img.height;
                    // img.scale(8 / img.height);
                    img.set({
                        left: item.position[0] * 8,
                        top: item.position[1] * 8,
                        originX: "center",
                        scaleX: scale,
                        scaleY: scale,
                    });
                    this.canvas.add(img);
                });
            });
        },
        //
        slectFile(file, fileList) {
            let tempUrl = window.URL.createObjectURL(file.raw);
            this.drawImage(tempUrl);
        },
        //
        async inputFile() {
            let files = await inputFile();
            this.loading = true;
            for (let i = 0; i < files.length; i++) {
                let image = await getAverageColor(files[i]);
                this.imgList.push(image);
            }
            console.log(this.imgList);
            this.loading = false;
        },
        //
        drawLine() {
            const blockPixel = 8;
            for (let i = 0; i <= this.canvas.width / blockPixel; i++) {
                this.canvas.add(
                    new fabric.Line([i * blockPixel, 0, i * blockPixel, this.canvas.height], {
                        left: i * blockPixel,
                        stroke: "gray",
                        selectable: false, //
                    })
                );
                this.canvas.add(
                    new fabric.Line([0, i * blockPixel, this.canvas.height, i * blockPixel], {
                        top: i * blockPixel,
                        stroke: "gray",
                        selectable: false, //
                    })
                );
            }
        },
        //
        getCanvasData() {
            // let blockList = [];
            for (let Y = 0; Y < this.canvas.height / 8; Y++) {
                for (let X = 0; X < this.canvas.width / 8; X++) {
                    //8*8
                    let tempColorData = this.ctx.getImageData(X * 8, Y * 8, 8, 8).data;
                    // console.log(X, Y, tempColorData);
                    //4,
                    this.blockList[Y * 100 + X] = { position: [X, Y], color: [] };
                    for (let i = 0; i < tempColorData.length; i += 4) {
                        this.blockList[Y * 100 + X].color.push([
                            tempColorData[i],
                            tempColorData[i + 1],
                            tempColorData[i + 2],
                            tempColorData[i + 3],
                        ]);
                    }
                }
            }
            console.log(mostBlockColor(this.blockList));
            this.mostBlockColor(this.blockList);
            this.loading = false;
        },
        //
        drawImage(url) {
            this.loading = true;
            fabric.Image.fromURL(url, (img) => {
                //,this.canvas.width / img.width,this.canvas.height / img.height
                let scale =
                    img.height > img.width
                        ? this.canvas.width / img.width
                        : this.canvas.height / img.height;
                img.set({
                    left: this.canvas.width / 2, //
                    originX: "center", //
                    originY: "center",
                    top: this.canvas.height / 2,
                    scaleX: scale, //
                    scaleY: scale, //
                    selectable: false, //
                });
                img.on("added", (e) => {
                    //,added,,
                    //
                    setTimeout(() => {
                        this.getCanvasData();
                    }, 500);
                });
                this.canvas.add(img); //
                // this.drawLine(); //
            });
        },
        //
        mostBlockColor(blockList) {
            //
            for (let i = 0; i < blockList.length; i++) {
                let r = 0,
                    g = 0,
                    b = 0,
                    a = 0;
                for (let j = 0; j < blockList[i].color[j].length; j++) {
                    r += blockList[i].color[j][0];
                    g += blockList[i].color[j][1];
                    b += blockList[i].color[j][2];
                    a += blockList[i].color[j][3];
                }

                // 
                r /= blockList[i].color[0].length;
                g /= blockList[i].color[0].length;
                b /= blockList[i].color[0].length;
                a /= blockList[i].color[0].length;
                // 
                r = Math.round(r);
                g = Math.round(g);
                b = Math.round(b);
                a = Math.round(a);
                this.blockMainColors.push({
                    position: blockList[i].position,
                    color: [r, g, b, a],
                });
            }
            console.log(this.blockMainColors);
            //
            /* for (let i = 0; i < blockList.length; i++) {
                let colorList = [];
                let rgbaStr = "";
                for (let k = 0; k < blockList[k].color.length; k++) {
                    rgbaStr = blockList[i].color[k];
                    if (rgbaStr in colorList) {
                        ++colorList[rgbaStr];
                    } else {
                        colorList[rgbaStr] = 1;
                    }
                }
                let arr = [];
                for (let prop in colorList) {
                    arr.push({
                        // rgb,`rgb(${prop})`
                        color: prop.split(","),
                        // color: `rgba(${prop})`,
                        count: colorList[prop],
                    });
                }
                // 
                arr.sort((a, b) => {
                    return b.count - a.count;
                });
                arr[0].position = blockList[i].position;
                this.blockMainColors.push(arr[0]);
            } */
            // console.log(this.blockMainColors);
        },
        //
        colorDiff(color1, color2) {
            let d = 0;
            for (let i = 0; i < color1.length; i++) {
                d += (color1[i] - color2[i]) ** 2;
            }
            return Math.sqrt(d);
        },
        //
        addCanvasEvent() {
            //
            this.canvas.on("after:render", (e) => {});
            //
            this.canvas.on("object:added", (e) => {});
            //
            this.canvas.on("mouse:down", (e) => {
                //ALT+,
                if (e.e.ctrlKey) {
                    let mouse = this.canvas.getPointer(e.e);
                    let x = parseInt(mouse.x);
                    let y = parseInt(mouse.y);
                    let px = this.ctx.getImageData(x, y, 1, 1).data;
                    console.log(
                        `%c x,y:(${x},${y})/rgba(${px[0]},${px[1]},${px[2]},${px[3]})`,
                        `background: rgba(${px[0]},${px[1]},${px[2]},${px[3]});`
                    );
                }
            });
            // 
            this.canvas.on("mouse:wheel", (e) => {
                e.e.preventDefault();
                e.e.stopPropagation();
                let ZOOM = 0.05;
                let point = new fabric.Point(this.canvas.width / 2 - 1, this.canvas.height / 2 - 1);
                //(alt + whell )
                if (!e.e.ctrlKey) {
                    return;
                } else if (e.e.altKey && e.e.ctrlKey) {
                    //(ctrl + alt + whell )
                    // ZOOM = 0.1;
                    point = new fabric.Point(e.pointer.x, e.pointer.y);
                }
                this.zoom = (e.e.deltaY > 0 ? -ZOOM : ZOOM) + this.canvas.getZoom();
                this.zoom = Math.max(0.1, this.zoom); //0.1
                this.zoom = Math.min(10, this.zoom); //10
                this.canvas.zoomToPoint(point, this.zoom);
            });
            //
            this.canvas.on({
                "mouse:down": (e) => {
                    if (e.e.altKey) {
                        this.panning = true;
                        this.canvas.selection = false;
                    }
                },
                "mouse:up": (e) => {
                    this.panning = false;
                    this.canvas.selection = true;
                },
                "mouse:move": (e) => {
                    if (this.panning && e && e.e) {
                        let delta = new fabric.Point(e.e.movementX, e.e.movementY);
                        this.canvas.relativePan(delta);
                    }
                },
            });
            this.canvas.on({
                //,
                "object:moving": function (e) {
                    e.target.opacity = 0.5;
                },
                //
                "object:modified": function (e) {
                    e.target.opacity = 1;
                },
            });
        },
        //
        exportCanvas() {
            const dataURL = this.canvas.toDataURL({
                width: this.canvas.width,
                height: this.canvas.height,
                left: 0,
                top: 0,
                format: "png",
            });
            const link = document.createElement("a");
            link.download = "canvas.png";
            link.href = dataURL;
            document.body.appendChild(link);
            link.click();
            document.body.removeChild(link);
        },
    },
};
</script>
<style lang="scss" scoped>
::v-deep .el-form-item__label {
    color: white !important;
}
::v-deep .el-upload-list {
    max-width: 200px;
    overflow: hidden;
    .el-upload-list__item-name {
        color: white;
    }
}
.home {
    transition: width 0.28s;
    width: 100vw;
    min-width: 1200px;
    height: 100vh;
    display: flex;
    .operations {
        background: #516fa3;
        padding: 20px;
        width: 300px;
        color: white;
        border: 1px solid #516fa3;
        display: flex;
        align-items: center;
    }
    .content {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        min-width: 820px;
        overflow: hidden;
        flex: 1;
    }
}
#canvas {
    border: 2px dashed #516fa3;
    object-fit: fill;
    background-color: white;
}
</style>
